export { randomInRange, shuffleArray };

function randomInRange(n: number): number {
    return Math.floor(Math.random() * n);
}

function shuffleArray<T>(array: Array<T>) {
    for (let i = array.length - 1; i > 0; --i) {
        let j = randomInRange(i + 1);
        let temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }
}
